
export const lastSeen = (lastVisited)=>{
  console.log(lastVisited)
  var d1 = new Date(); //"now"
  var d2 = new Date(lastVisited+"UTC")  // some date
  var diff = Math.abs(d1-d2);  // difference in milliseco
  console.log(diff)
  var time = ''
  console.log(Math.floor((diff / (1000 * 60 * 60)) % 24))
  if(Math.floor(diff / 60000) < 60){
    if(Math.floor(diff / 60000 > 1)){
      time = String(Math.floor(diff / 60000)) +' minutes ago'
    }else{
      time = String(Math.floor(diff / 60000)) +' minute ago'
    }
  }
  else if(Math.floor(diff / (3600000)) < 24){
    if(Math.floor(diff / (3600000)) > 1){
      time = String(Math.floor(diff / (3600000))) + ' hours ago'
    }else{
      time = String(Math.floor(diff / (3600000))) + ' hour ago'
  }
  }else{
    if(Math.floor((diff / (60*60*24*1000))) > 1){
      time = String(Math.floor((diff / (60*60*24*1000)))) + ' days ago'
    }else{
      time = String(Math.floor((diff / (60*60*24*1000)))) + ' day ago'
    }
  }
  return time
}

export const localTime = (lastVisited)=>{
  var date = new Date(lastVisited+"UTC");
  return(date.toString())
}
