import React,{useState} from 'react'
import axios from 'axios'
import FileDropzone from './FileDropzone'
import FaceDisplay from './FaceDisplay'
import Loading from './Loading'


function FaceRecognitionContainer(){
  const [state, setState] = useState(null)
  const [file, setFile] = useState(null)
  const [items, setItems] = useState({personId: 'default'})
  const [loading, setLoading] = useState(false)
  const [style, setStyle] = useState({display: 'none'})
  const proxyurl = 'https://cors-anywhere.herokuapp.com/';
  const POST_URL = 'https://p9xcxvjfgf.execute-api.us-east-2.amazonaws.com/v1/search'
  const fileSelectedHandler = e =>{
    setFile(e.target.files[0])
  }

  const fileUploadHandler = (fileBase) =>{
    setStyle({display: 'none'})
    setLoading(true)
    let base64String = String(fileBase)
    axios.post('https://cors-anywhere.herokuapp.com/https://p9xcxvjfgf.execute-api.us-east-2.amazonaws.com/v1/search',
      {
        imageData: base64String
   })
    .then(response => {
        console.log(response)
      if(response.data == "No face detected in image"){
        setLoading(false)
        setState('noFace')
      }
      else if(response.data.personId == 'none' ){
        setLoading(false)
        setState('notValid')
      }else if(response.data.personId !== 'none'){ 
        setState('valid')
        setLoading(false)
        setTimeout(() => {
          setItems({personId: response.data.personId})
          setStyle({display: 'block'})
        }, 500);

      }
    })
    .catch(error =>{
      console.log(error)
    })

  }
  const handleSubmit = (e,items) => {
    e.preventDefault();
      axios.patch('https://cors-anywhere.herokuapp.com/https://p9xcxvjfgf.execute-api.us-east-2.amazonaws.com/v1/persons',
        {
          personId: items.personId,
          blacklisted: items.blacklisted,
          personName: items.name
      })
      .then(response => {
        console.log(response)
      })
      .catch(error =>{
        console.log(error)
      })
}

  const stateChange = (image)=>{
    if(image){
      let reader = new FileReader();
      reader.readAsDataURL(image);
      reader.onloadend = () => {
        //setFile(reader.result)
        fileUploadHandler(reader.result)
      };

      //setState('valid')
    }

    }
  const faceDisplay = ()=>{
     if(state =='notValid'){
      return <h1 style={{display: 'flex', justifyContent: 'center', fontSize: '40px', color:'lightblue'}}>Sorry, that person was not found. Please try again!</h1>
    }else if( state === 'noFace'){
      return <h1 style={{display: 'flex', justifyContent: 'center', fontSize: '40px', color:'lightblue'}} >Sorry, no face was detected in the image. Please try again!</h1>
    }
  }

  return(
    <div className='facerecog-container'>
      <div style={style}><FaceDisplay handleSubmit={handleSubmit} setLoading={setLoading} items={items}/></div>
      {loading ?
        <div className='loading-container'>
             <div className='loading-bar'><Loading /></div>
          </div> :
      faceDisplay()}
      <FileDropzone  handleChange={stateChange}/>
    </div>
  )
}


export default FaceRecognitionContainer
