import React from 'react'
import {localTime} from '../formatDate'



function DashboardActivityCustomersComponent(props){

  return(
    <div className='customer-component'>
      <div className='customer-component-img'>
          <img src={props.img} alt='image'/>
      </div>
      <div className='customer-component-information'>
        <h3><b>{props.name}</b></h3>
        <p>Last Visited: <b>{localTime(props.lastVisited).slice(0,21)}</b></p>
        <p>Blacklisted: <b>{props.blackListed ? 'true': 'false'}</b></p>
      </div>

    </div>
  )
}

export default DashboardActivityCustomersComponent
