
import React,{useState,useEffect} from 'react';
import './App.css';
import DashboardContainer from './components/DashboardContainer'
import SideBar from './components/SideBar'
import CameraViewContainer from './components/CameraViewContainer'
import FaceContainer from './components/FaceContainer'
import FaceRecognitionContainer from './components/FaceRecognitionContainer'
import {BrowserRouter as Router, Switch, Route} from 'react-router-dom'
import {PeopleProvider} from './components/PeopleContext'
import { MuiThemeProvider, createMuiTheme } from '@material-ui/styles';
import LogIn from './components/auth/LogIn'
import Register from './components/auth/Register'
import {Auth} from 'aws-amplify'



function App() {
  const [isAuthenticated, setAuthStatus] = useState(false)
  const [user, setUser] = useState(null)
  const [isAuthenticating, setIsAuthenticating] = useState(true)



  useEffect(() => {
  //  let didCancel = false;
    console.log('app')

    async function session() {
      try{
        console.log('this deffff')
        const sessionVar = await Auth.currentSession();
        setAuthStatus(true);
        console.log(sessionVar);
        const username = await Auth.currentAuthenticatedUser();
        //setUser(username);
        console.log(username)
        console.log('right abooooove')
      }catch(error){
        console.log(error)
        if(error === 'No current user'){
          setAuthStatus(false)
        }

      }
        }

    session();
    setIsAuthenticating(false)
  //return () => { didCancel = true; };
}, []);


  const authProps = {
    isAuthenticated: isAuthenticated,
    user: user,
    setAuthStatus: setAuthStatus,
    setUser: setUser
  }

if(!isAuthenticated){
    return(<LogIn auth={authProps}/> )
  }else{
  return (
    !isAuthenticating &&
    <PeopleProvider auth={authProps}>
      <Router>
        <div className='app'>
          <div className='side-bar'><SideBar/></div>
          <div className='page'>
            <Switch >
              <Route exact path='/' render={(props) => <DashboardContainer {...props} auth={authProps} />} />
              <Route path='/cameraview' render={(props) => <CameraViewContainer {...props} auth={authProps}/>} />
              <Route path='/faces' render={(props) => <FaceContainer {...props} auth={authProps}/>}/>
              <Route path='/facerecognition' render={(props) => <FaceRecognitionContainer {...props} auth={authProps}/>}/>
            </Switch>
        </div>
        </div>
    </Router>

  </PeopleProvider>
  )
}

}

export default App;
