import React,{useState,useEffect} from 'react'
import CameraViewComponent from './CameraViewComponent'
import tempImage from '../imgs/campaign-revenue.png'
import Loading from './Loading'

function CameraViewContainer(){
  const proxyurl = "https://cors-anywhere.herokuapp.com/";
  const FETCH_URL = 'https://p9xcxvjfgf.execute-api.us-east-2.amazonaws.com/v1/cameras'
  const [cams, setCams] = useState([])
  const [loading, setLoading] = useState(true)

    useEffect(()=>{
      fetch(proxyurl + FETCH_URL) // https://cors-anywhere.herokuapp.com/https://example.com
      .then(response => response.json())
      .then((contents) =>{
        console.log(contents)
        setCams(contents.camerasList)
        setLoading(false)

        //setRecentPeople(contents.lastPersons)
        // setStats(contents.stats)
      })
     .catch(() => console.log("Can’t access " + FETCH_URL + " response. Blocked by browser?"))

   },[])


  if(loading){
    return <Loading/>
  }else{
  return(
    <div className='cameraview-container'>
      <h1> Camera View</h1>
      <div className='cameraview-component-container'>
        {cams.map(obj =>
          <CameraViewComponent key={obj.cameraId} cameraId={obj.cameraId} name={obj.cameraName} imageURL={obj.lastImageURL}/>
        )}
      </div>
  </div>
  )
}
}
export default CameraViewContainer
