import React,{useState,useContext} from 'react'
import TotalVisitorsGraphDay from './TotalVisitorsGraphDay'
import TotalVisitorsGraphMonth from './TotalVisitorsGraphMonth'
import TotalVisitorsGraphWeek from './TotalVisitorsGraphWeek'
import TotalVisitorsGraphYear from './TotalVisitorsGraphYear'
import DashboardProfitability from './DashboardProfitability'
import { makeStyles } from '@material-ui/core/styles';
import ToggleButton from '@material-ui/lab/ToggleButton';
import ToggleButtonGroup from '@material-ui/lab/ToggleButtonGroup';
import {PeopleContext} from './PeopleContext'
import {Auth} from 'aws-amplify'

function DashboardGraphContainer(props){

  const [value, setValue] = useState('day')
  const {auth,user} = useContext(PeopleContext)
  const [username, setUsername] = user
  const [isAuthenticated, setIsAuthenticated] = auth

  console.log(Auth)

  const handleChange = (event, newAlignment) => {
      if (newAlignment !== null) {
        setValue(newAlignment);
      }
    };

  const graph = ()=>{
    switch(value){
      case 'day':
        return <TotalVisitorsGraphDay stats={props.stats}/>
      case 'week':
        return <TotalVisitorsGraphWeek stats={props.stats}/>
      case 'month':
        return <TotalVisitorsGraphMonth stats={props.stats}/>
      case 'year':
        return <TotalVisitorsGraphYear stats={props.stats}/>
      default:
        return null
    }

  }
  //{isAuthenticated && username && (
  //  <h1>Hello {username.username}</h1>)}

  return(
    <div className='dashboard-top-container'>

      {isAuthenticated && username && (
       <h1>Hello {username.username}</h1>)}

      <div className='dashboard-graph-container'>
        <div className='dashboard-overview-graph'>
            <ToggleButtonGroup
              value={value}
              exclusive
              onChange={handleChange}
              aria-label="text alignment"
              className='toggle'
            >
              <ToggleButton value="day" aria-label="left aligned">
                24 hrs
              </ToggleButton>
              <ToggleButton value="week" aria-label="left aligned">
                7 days
              </ToggleButton>
              <ToggleButton value="month" aria-label="centered">
                30 days
              </ToggleButton>
              <ToggleButton value="year" aria-label="right aligned">
                12 months
              </ToggleButton>
            </ToggleButtonGroup>
            <div className='dashboard-graph'>
              {graph()}
            </div>

        </div>
        <div className='dashboard-overview-people'><DashboardProfitability stats={props.stats} topPeople={props.topPeople}/></div>
      </div>
    </div>
  )
}

export default DashboardGraphContainer
