import React,{useState} from 'react'
import DashboardActivityNotificationsComponent from './DashboardActivityNotificationsComponent'


function DashboardActivityNotifications(props){
  const [notifications, setNotifications] = useState(props.notifications)

  return(
    <div className='alert-container'>
      <div className='alert-container-2'>
        <ul>
            {notifications.map(obj =>
              <DashboardActivityNotificationsComponent
                key={obj.personId + obj.timestamp}
                name={obj.personName}
                lastVisited={obj.timestamp}
              />
            )}
        </ul>
      </div>
    </div>
  )
}

export default DashboardActivityNotifications
