import React from 'react'
import {localTime} from '../formatDate'

function Person(props){
  //console.log('hereeeeee')
  //console.log(props)
  return(
    <div className='customer-component customer-hover' onClick={() => props.onClick({
      name: props.name,
      lastVisited: props.lastVisited,
      img: props.img,
      blacklisted: props.blacklisted,
      personId: props.personId
    })}>
      <div className='customer-component-img'>
        <img src={props.img} alt='image'/>
      </div>
      <div className='customer-component-information'>
        <h3><b>{props.name}</b></h3>
        <p>Last Visited: <b>{localTime(props.lastVisited).slice(0,21)}</b></p>
        <p>Blacklisted: <b>{props.blacklisted ? 'true': 'false'}</b></p>
      </div>

    </div>
  )
}

export default Person
