import React,{useState} from 'react'
import axios from 'axios'

function CameraViewComponent(props){
  const [value, setValue] = useState(props.name)

  const handleChange = e =>{

    setValue(e.target.value)
  }

  const handleSubmit = e =>{
      e.preventDefault();
      axios.patch('https://cors-anywhere.herokuapp.com/https://p9xcxvjfgf.execute-api.us-east-2.amazonaws.com/v1/cameras',
        {
          cameraId: props.cameraId,
          cameraName: value
     })
      .then(response => {
        alert("Updated!")
      })
      .catch(error =>{
        console.log(error)
      })
  }

  return(
    <div className='camera-component-container'>
      <form onSubmit={handleSubmit}>
        <input type='text' onChange={handleChange} value={value}/>
      </form>
      <img src={props.imageURL} alt='camera'/>

    </div>
  )
}

export default CameraViewComponent
