import React,{useState, useEffect, useContext} from 'react'
import FaceDisplay from './FaceDisplay'
import Person from './Person'
import tempPic from '../imgs/pictureOfMe.jpg'
import {PeopleContext} from './PeopleContext'
import axios from 'axios'
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import Loading from './Loading'

function FaceContainer(){
  //will set people to either recentPeople or visitedPeople
  const [people, setPeople] = useState([])

  const [recentPeople, setRecentPeople] = useState([])
  const [mostPeople, setMostPeople] = useState([])
  const [currentPerson, setCurrentPerson] = useState(0)
  const [select, setSelect] = useState('mostVisited')
  const [display, setDisplay] = useState({name: 'default', personId: 'default', blacklisted: true, img:'default'})
  const FETCH_URL_RECENT = 'https://p9xcxvjfgf.execute-api.us-east-2.amazonaws.com/v1/persons?size=28&sort=recent'
  const FETCH_URL_VISITED = 'https://p9xcxvjfgf.execute-api.us-east-2.amazonaws.com/v1/persons?size=28&sort=most'
  const proxyurl = "https://cors-anywhere.herokuapp.com/"
  const [loading, setLoading] = useState(true)

  useEffect(()=>{
    if(select != 'mostVisited'){
      console.log('most recent called')
      setLoading(true)
      fetch(proxyurl + FETCH_URL_RECENT) // https://cors-anywhere.herokuapp.com/https://example.com
      .then(response => response.json())
      .then((contents) =>{
        //console.log(contents.list)
        //recentPeople = contents.list
        //setRecentPeople(contents.list)
        setPeople(contents.list)
        setLoading(false)
      })
     .catch(() => console.log("Can’t access " + FETCH_URL_RECENT + " response. Blocked by browser?"))
 }
 },[select])

  useEffect(()=>{
    if(select == 'mostVisited'){
      console.log('most visited called')
      setLoading(true)
       fetch(proxyurl + FETCH_URL_VISITED) // https://cors-anywhere.herokuapp.com/https://example.com
       .then(response => response.json())
       .then((contents) =>{
            //setMostPeople(contents.list)
            setPeople(contents.list)
            setLoading(false)

           //mostPeople = contents.list

       })
      .catch(() => console.log("Can’t access " + FETCH_URL_VISITED + " response. Blocked by browser?"))

    }

},[select])

  useEffect(()=>{

    if (!(Object.keys(people).length === 0)){
      console.log(typeof(people))
      //console.log(people[0].personId)
      //[personId, imageURL, personName, timestamp, blacklisted] = {people[0]}

      setDisplay({
        personId: people[currentPerson].personId,
        key: people[currentPerson].personId,
        img: people[currentPerson].imageURL,
        name: people[currentPerson].personName,
        lastVisited: people[currentPerson].timestamp,
        blacklisted: people[currentPerson].blacklisted})
    }
    //deleted people, add if causes issues
  },[people])


  const setRecent = (recentPeople)=>{
    let newPeople = {}
    for(let i=0; i<recentPeople.length; i++){
      newPeople.push(recentPeople[i])
    }
    console.log(newPeople)

  }



  const handleSubmit = (e,items) => {
    e.preventDefault();
    if(items.name){
      console.log(typeof(items.blacklisted))
      let newPeople = []
      for (let i = 0; i < people.length; i++){
        if(items.personId == people[i].personId){
          console.log(items.blacklisted)
          newPeople.push({
            personId: items.personId,
            imageURL: people[i].imageURL,
            blacklisted: items.blacklisted,
            timestamp: people[i].timestamp,
            personName: items.name })
          setCurrentPerson(i)
        }else{
          newPeople.push(people[i])
        }
      }
      setPeople(newPeople)
      axios.patch('https://cors-anywhere.herokuapp.com/https://p9xcxvjfgf.execute-api.us-east-2.amazonaws.com/v1/persons',
        {
          personId: items.personId,
          blacklisted: items.blacklisted,
          personName: items.name
     })
      .then(response => {
        console.log(response)
      })
      .catch(error =>{
        console.log(error)
      })
    }else{
      alert("Please enter a valid name!")
    }

 }


  const onClick = (event) =>{
    setDisplay(event)
  }

  const handleChange = (e) => {
      setSelect(e.target.value);
      //if (e.target.value == 'mostVisited'){
      //  setPeople(mostPeople)
      //}else{
      //  setPeople(recentPeople)
      //}
      //setCurrentPerson(0)
    };

//  {display != undefined ? <FaceDisplay items={display}/> : <h1>Loading..</h1> }
//  <FaceDisplay items={display}/>
//
  if(loading){
    return(
      <div className='loading-container'>
        <div className='loading-bar'><Loading /></div>
      </div>
    )
  }else{
  return(
    <div className='face-container'>
      <FaceDisplay handleSubmit={handleSubmit} items={display}/>
      <div className='face-bottom-container'>
        <Select
          className='most-selector'
          value={select}
          onChange={handleChange}
        >
          <MenuItem value={'mostVisited'}>Most Visited</MenuItem>
          <MenuItem value={'mostRecent'}>Most Recent</MenuItem>
        </Select>
        <div className='customers-container' >
          {people.map(obj =>
            <Person
              key={obj.personId}
              personId={obj.personId}
              img={obj.imageURL}
              name={obj.personName}
              lastVisited={obj.timestamp}
              blacklisted={obj.blacklisted}
              onClick={onClick}
            />

          )}
        </div>
      </div>

    </div>
  )}
}

export default FaceContainer
