import React,{useState, useContext} from 'react'
import DashboardActivityCustomersComponent from './DashboardActivityCustomersComponent'



function DashboardActivityCustomers({people}){
  //const {recentPeople} = useContext(PeopleContext)
  //const [customers, setCustomers] = recentPeople


  return(
    <div className='customers-container'>
      {people.map(obj =>
        <DashboardActivityCustomersComponent
          key={obj.personId}
          img={obj.imageURL}
          name={obj.personName}
          lastVisited={obj.timestamp}
          blackListed={obj.blacklisted}/>
      )}
    </div>
  )
}

export default DashboardActivityCustomers
