import React,{useEffect,useState} from 'react'
import DashboardGraphContainer from './DashboardGraphContainer'
import DashboardActivityContainer from './DashboardActivityContainer'
import Loading from './Loading'
import {Auth} from 'aws-amplify'

function DashboardContainer(){
  const FETCH_URL = 'https://p9xcxvjfgf.execute-api.us-east-2.amazonaws.com/v1/dashboard'
  const FETCH_URL_VISITED = 'https://p9xcxvjfgf.execute-api.us-east-2.amazonaws.com/v1/persons?size=3&sort=most'
  const proxyurl = "https://cors-anywhere.herokuapp.com/";
  const [recentPeople, setRecentPeople] = useState([])
  const [stats, setStats] = useState({})
  const [topPeople, setTopPeople] = useState([])
  const [notifications, setNotifications] = useState([])
  const [loading, setLoading] = useState(true)

//  Auth.currentSession()
//    .then(data => console.log(data))
//    .catch(err => console.log(err));

  useEffect(()=>{
    fetch(proxyurl + FETCH_URL) // https://cors-anywhere.herokuapp.com/https://example.com
    .then(response => response.json())
    .then((contents) =>{
      setNotifications(contents.notifications)
      setRecentPeople(contents.lastPersons)
      setStats(contents.stats)
    })
   .catch(() => console.log("Can’t access " + FETCH_URL + " response. Blocked by browser?"))
      },[])

    useEffect(()=>{

        fetch(proxyurl + FETCH_URL_VISITED) // https://cors-anywhere.herokuapp.com/https://example.com
        .then(response => response.json())
        .then((contents) =>{

          setTopPeople(contents.list)
          setLoading(false)
        })
       .catch(() => console.log("Can’t access " + FETCH_URL_VISITED + " response. Blocked by browser?"))

   },[])

   if(loading){
     return(
       <div className='loading-container'>
         <div className='loading-bar'><Loading /></div>
       </div>
     )
   }else{
  return(
    <div className='dashboard-container'>
      <DashboardGraphContainer topPeople={topPeople} stats={stats} timePeriod='day'/>
      <DashboardActivityContainer  notifications={notifications} people={recentPeople}/>
    </div>
  )
}
}

export default DashboardContainer
